@use 'variables' as v;
@use 'mixins' as m;

h1, h2, h3 {
    @include m.titulo;
}

h1 {
    font-size: 4rem;
}
h2 {
    font-size: 3.6rem;
}
h3 {
    font-size: 3rem;
}
.text-center {
    text-align: center;
}

[class $= "__heading"] { // $ = ends with, ^= starts with, *= contains. 
    @include m.nombre-pagina;
}

[class $= "__descripcion"] { // $ = ends with, ^= starts with, *= contains. 
    @include m.descripcion-pagina;
}