@use 'base/mixins' as m;
@use 'base/variables' as v;

.agenda {
    padding: 5rem 0;
    @include m.contenedor;
}

.eventos {
    $modificador: &;

    &__heading {
        color: v.$azul;
        margin: 5rem 0;
        text-align: left;
        font-size: clamp(2rem, -0.8rem + 8vw, 6rem);
    }

    &__fecha {
        color: v.$gris;
        margin: 3rem 0 1rem 0;
    }
    
    &--workshops {
        // Este es un selector anidado especial que utiliza la variable $modificador para combinarla con '__heading'. 
        //En este caso, el selector resultante sera '.eventos--workshops eventos__heading'. Se llama anidamiento
        //El # indica que va a haber una mezcla entre una variable y un string
        #{$modificador}__heading {
            color: v.$secundario; 
        }

        .evento__informacion {
            background-color: v.$secundario;
            &:hover {
                background-color: darken(v.$secundario, 10%);
            }
        }
    }

    .swiper-button-next, 
    .swiper-button-prev {
        color: rgba(v.$blanco, .5);
        font-size: 2rem;
        transition: color .3s ease;

        &:hover {
            color: v.$blanco;
        }
    }
}

.evento {
    &__hora {
        font-weight: v.$bold;
    }

    &__informacion {
        padding: 2rem;
        border-radius: 1rem;
        background-color: v.$azul;
        color: v.$blanco;
        transition: background-color .3s ease;

        &:hover {
            background-color: darken(v.$azul, 10%);
        }
    }

    &__nombre {
        display: -webkit-box;
        -webkit-line-clamp: 1;
        -webkit-box-orient: vertical;
        overflow: hidden;
        font-size: 2rem;
        margin: 0;
    }

    &__introduccion {
        font-size: 1.6rem;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        overflow: hidden;
    }

    &__autor-info {
        display: flex;
        gap: 2rem;
        justify-content: space-between;
    }

    &__imagen-autor{
        width: 5rem;
        background-color: v.$blanco;
        border-radius: 50%;
    }

    &__autor-nombre {
        font-weight: v.$bold;
    }

    &__agregar {
        @include m.boton(v.$blanco, v.$negro);
        margin-top: 1rem;

        &:disabled {
            opacity: .5;
            cursor: not-allowed;
        }
    }
}